import React from "react";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";

import axios from "axios";

import { API_URL } from "../constants";

class Signup extends React.Component {
  state = {
    pk: 0,
    teamname: "",
    member1: "",
    email1:"",
    member2: "",
    email2:"",
    member3: "",
    email3:"",
    member4: "",
    email4:"",
    
  };

  componentDidMount() {
    if (this.props.student) {
      const {
           pk, 
           teamname,
            member1,
            email1,
            member2,
            email2,
            member3,
            email3,
            member4,
            email4
        } = this.props.student;
      this.setState({ 
          pk, 
          teamname, 
          member1,
          email1,
          member2,
          email2,
          member3,
          email3,
          member4,
          email4
        });
    }
  }

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  createStudent = e => {
    e.preventDefault();
    axios.post(API_URL, this.state).then(() => {
      this.props.resetState();
      this.props.toggle();
    });
  };

  editStudent = e => {
    e.preventDefault();
    axios.put(API_URL + this.state.pk, this.state).then(() => {
      this.props.resetState();
      this.props.toggle();
    });
  };

  defaultIfEmpty = value => {
    return value === "" ? "" : value;
  };

  render() {
    return (
      <Form onSubmit={this.props.student ? this.editStudent : this.createStudent}>
        <FormGroup>
          <Label for="name">Team Name:</Label>
          <Input
            type="text"
            name="teamname"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.name)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="member1">Member 1 Name:</Label>
          <Input
            type="text"
            name="member1"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.name)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="email">Member 1 Email:</Label>
          <Input
            type="email"
            name="email1"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.email)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="member1">Member 2 Name:</Label>
          <Input
            type="text"
            name="member2"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.name)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="email">Member 2 Email:</Label>
          <Input
            type="email"
            name="email2"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.email)}
          />
        </FormGroup><FormGroup>
          <Label for="member1">Member 3 Name:</Label>
          <Input
            type="text"
            name="member3"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.name)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="email">Member 3 Email:</Label>
          <Input
            type="email"
            name="email3"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.email)}
          />
        </FormGroup><FormGroup>
          <Label for="member1">Member 4 Name:</Label>
          <Input
            type="text"
            name="member4"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.name)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="email">Member 4 Email:</Label>
          <Input
            type="email"
            name="email4"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.email)}
          />
        </FormGroup>
        <Button>Send</Button>
      </Form>
    );
  }
}

export default Signup;